<template>
    <div>
        <div class="container">
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column prop="position_name" label="岗位" width="120">
                </el-table-column>
                <el-table-column label="系统页面">
                    <template slot-scope="{row, $index}">
                        <el-tag v-for="item in row.auth_name">{{item.auth_name}}</el-tag>
                        <!-- <el-tag>{{row.auth_name}}</el-tag> -->
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-edit" @click="handleEdit(row, $index)">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
                </el-pagination>
            </div> -->
        </div>

        <!-- 编辑页面权限 -->
        <el-dialog title="编辑页面权限" :visible.sync="dialogPage" width="50%">
            <el-form ref="formPage" :model="formPage" label-width="80px" label-position="right">
                <el-form-item label="岗位">
                    {{formPage.position_name}}
                </el-form-item>
                <el-form-item label="页面权限">
                    <el-tree
                        ref="domTree"
                        :data="pageListAll"
                        show-checkbox
                        node-key="auth_value"
                        default-expand-all
                        :expand-on-click-node="false">
                        <span class="custom-tree-node" slot-scope="{ node, data }">
                            <span>{{ data.route_name }}</span>
                        </span>
                    </el-tree>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogPage = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitPage">确 定</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
    import * as API from '@/api/enterpriseManagement'
    export default {
        data() {
            const data = [];
            return {
                tableData: [],
                page: 1,
                pageListAll: JSON.parse(JSON.stringify(data)),
                dialogPage: false,
                loading: false,
                formPage: {
                    auth_value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }]
            }
        },
        created() {
            this.getData();
        },
        computed: {

        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                this.loading = true
                API.pagePermissionList({}).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                })
            },
            getTreeList(){
                API.pageList().then(res => {
                    this.pageListAll = JSON.parse(JSON.stringify(res.data))
                    setTimeout(()=>{
                        this.$refs.domTree.setCheckedNodes(this.formPage.auth_name)
                    },100)
                })
            },
            handleEdit(row, index) {
                this.dialogPage = true;
                const item = this.tableData[index];
                Object.assign(this.formPage,row)
                this.getTreeList()
                
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            fnAddAuthValue(items){
                var sum = 0;
                for(var i= 0 ;i< items.length; i++) {
                    sum += parseInt(items[i].auth_value);
                }
                return sum;
            },
            dialogSubmitPage(){
                // this.dialogPage = false
                let params = {
                    id: this.formPage.id,
                    auth_value: this.fnAddAuthValue(this.$refs.domTree.getCheckedNodes())
                }
                
                API.pagePermissionEdit(params).then(res => {
                    this.dialogPage = false
                    this.getData()
                })
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
